/* General Styles */
.first {
    display: flex;
    justify-content: center;
    align-items: center;
    
    color: white;
    padding: 2rem;
    text-align: center;
}

.main {
    display: flex;
    justify-content: flex-start; /* Align cards to the start */
    overflow-x: auto; /* Enable horizontal scrolling */
    padding: 2rem;
    background-color: #f7fafc; /* Light gray background */
    gap: 1.5rem; /* Space between cards */
}

.image {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap; /* Wrap items on smaller screens */
    gap: 2rem; /* Space between items */
    margin-top: 2rem;
}

.Datastructure, .Algorithm {
    border-radius: 50%;
    background-color: #0dd4ee; /* Light blue background */
    padding: 1.5rem;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    transition: transform 0.3s ease;
}

.Datastructure:hover, .Algorithm:hover {
    transform: scale(1.1); /* Slight zoom on hover */
}

.Datastructure img, .Algorithm img {
    max-height: 100px; /* Adjust image size */
    width: auto;
}

h2 {
    margin-top: 1rem;
    font-size: 1.5rem;
    color: #2d3748; /* Dark gray text */
}

h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: #2d3748; /* Dark gray text */
}

/* Card Styles */
.card {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    margin: 1rem 0;
    width: 300px; /* Fixed width for cards */
    height: 150px; /* Fixed height for cards */
    overflow: hidden; /* Prevent content overflow */
    animation: float 3s ease-in-out infinite; /* Y-direction animation */
    flex-shrink: 0; /* Prevent cards from shrinking */
}

@keyframes float {
    0%, 100% {
        transform: translateY(0); /* Start and end at original position */
    }
    50% {
        transform: translateY(-10px); /* Move up by 10px */
    }
}

.card h4 {
    font-size: 1.25rem;
    color: #2d3748; /* Dark gray text */
    margin-bottom: 0.5rem;
}
.g>h1{
 text-align: center;
}

.card p {
    font-size: 1rem;
    color: #718096; /* Light gray text */
    margin-top: 0.5rem;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .first {
        flex-direction: column;
        padding: 1rem;
    }

    .main {
        padding: 1rem;
        flex-direction: column; /* Stack cards vertically on smaller screens */
        overflow-x: hidden; /* Disable horizontal scrolling */
    }

    .image {
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }

    .Datastructure, .Algorithm {
        padding: 1rem;
    }

    h1 {
        font-size: 2rem;
    }

    h2 {
        font-size: 1.25rem;
    }

    .card {
        width: 100%; /* Full width on smaller screens */
        height: auto; /* Auto height for smaller screens */
    }

    .card h4 {
        font-size: 1rem;
    }

    .card p {
        font-size: 0.875rem;
    }
}