/* Root variables for easy theming */
:root {
    --primary-color: #1976d2;
    --primary-hover: #115293;
    --secondary-color: #dc004e;
    --secondary-hover: #9a0036;
    --background-light: #f9f9f9;
    --border-color: #ddd;
    --text-color: #333;
    --node-size: clamp(30px, 8vw, 50px); /* Larger nodes on small screens */
}

/* Graph Visualizer Container */
.graph-visualizer {
    text-align: center;
    margin-top: 20px;
    font-family: 'Arial', sans-serif;
    padding: 10px;
}

/* Button Styling */
.graph-visualizer .MuiButton-root {
    margin: 5px;
    padding: 12px 24px;
    font-size: clamp(12px, 3vw, 16px); /* Larger font on small screens */
    border-radius: 5px;
    transition: background-color 0.3s ease, transform 0.2s ease;
    min-width: 44px; /* Minimum touch target */
    min-height: 44px;
}

.graph-visualizer .MuiButton-root:active {
    transform: scale(0.95);
}

.graph-visualizer .MuiButton-containedPrimary {
    background-color: var(--primary-color);
    color: white;
}

.graph-visualizer .MuiButton-containedPrimary:hover {
    background-color: var(--primary-hover);
}

.graph-visualizer .MuiButton-containedSecondary {
    background-color: var(--secondary-color);
    color: white;
}

.graph-visualizer .MuiButton-containedSecondary:hover {
    background-color: var(--secondary-hover);
}

/* Graph Container */
.graph-container {
    position: relative;
    width: 100%;
    max-width: 100%; /* Full width on small screens */
    height: 60vh; /* Use viewport height for responsiveness */
    margin: 0 auto;
    border: 1px solid var(--border-color);
    background-color: var(--background-light);
    border-radius: 8px;
    overflow: auto; /* Allow scrolling */
    touch-action: pan-x pan-y; /* Improve touch response */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    scale: 0.95;
    transform-origin: center;
    transition: scale 0.3s ease-in-out;
}

.graph-container:hover {
    scale: 1;
}

/* Node Styling */
.node {
    position: absolute;
    width: var(--node-size);
    height: var(--node-size);
    background-color: lightblue;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: clamp(12px, 3vw, 16px); /* Larger font on small screens */
    color: var(--text-color);
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.node.visited {
    background-color: orange;
}

/* Arrow Styling */
.arrow {
    position: absolute;
    width: 2px;
    height: 2px;
    background-color: black;
    transform: rotate(45deg);
    opacity: 0;
    transition: opacity 0.5s ease, transform 0.5s ease;
}

/* Responsive Adjustments for Small Screens */
@media (max-width: 600px) {
    .graph-container {
        height: 50vh; /* Smaller height for very small screens */
    }

    .node {
        width: clamp(25px, 10vw, 40px); /* Even larger nodes on very small screens */
        height: clamp(25px, 10vw, 40px);
        font-size: clamp(10px, 2.5vw, 14px); /* Larger font on very small screens */
    }
}

/* Dark Mode Support */
@media (prefers-color-scheme: dark) {
    :root {
        --background-light: #1e1e1e;
        --border-color: #444;
        --text-color: #f9f9f9;
    }

    .graph-container {
        background-color: var(--background-light);
    }

    .node {
        background-color: #2c3e50; /* Darker node color for dark mode */
        color: var(--text-color);
    }

    .node.visited {
        background-color: #e74c3c; /* Highlight color for dark mode */
    }

    .arrow {
        background-color: #f9f9f9; /* Light arrow color for dark mode */
    }
}
