/* General Styles */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #f9f9f9;
}

header {
    background-color: #1a365d; /* Dark blue background */
    color: white;
    padding: 1rem;
    text-align: center;
    width: 100%;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h1 {
    font-size: 2.5rem;
    font-weight: bold;
    text-align: center;
    margin: 20px 0;
    color: #1a365d; /* Dark blue text */
}

h4 {
    font-size: 1.5rem;
    font-weight: bold;
    margin: 15px 0;
    color: #1a365d; /* Dark blue text */
}

p {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #333;
    margin: 10px 0;
}

ul {
    list-style-type: disc;
    margin: 10px 0;
    padding-left: 20px;
}

ul li {
    font-size: 1rem;
    color: #333;
    margin: 5px 0;
}

/* Image Styles */
img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 20px auto;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Main container for the stack component */
.Stack {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    padding: 20px;
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 90%;
    max-width: 1200px;
    margin: 20px auto;
}

/* Buttons for stack operations */
button {
    margin: 10px;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    background-color: #007BFF;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* Display area for stack elements */
.Stac {
    font-size: 1.2rem;
    margin-bottom: 20px;
    color: #333;
    font-weight: bold;
}

/* Stack visualization container */
.Stack-Show {
    display: flex;
    justify-content: space-between; /* Space between the two child divs */
    align-items: stretch; /* Stretch children to match height */
    background-color: #ffc107;
    border-radius: 12px;
    padding: 20px;
    border: 2px solid #ff9800;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-top: 20px;
    width: 90%;
    max-width: 400px;
    min-height: 200px; /* Minimum height for visualization */
}

/* Arrows section */
.arow {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%; /* Match parent height */
    flex-grow: 1; /* Allow arrows to grow with stack */
}

/* Styling for head arrow */
.Arro-head {
    background-color: orange;
    display: flex;
    align-items: center;
    font-weight: bold;
    padding: 8px;
    border-radius: 6px;
    color: white;
    gap: 5px;
    margin-top: 0; /* Head at the top */
    font-size: 1rem; /* Match arrow size with text */
    height: 40px; /* Match h1 size */
    width: 100px; /* Match h1 size */
    justify-content: center;
}

/* Styling for tail arrow */
.Arro-tail {
    background-color: green;
    display: flex;
    align-items: center;
    font-weight: bold;
    padding: 8px;
    border-radius: 6px;
    color: white;
    gap: 5px;
    margin-bottom: 0; /* Tail at the bottom */
    visibility: hidden; /* Hidden by default */
    font-size: 1rem; /* Match arrow size with text */
    height: 40px; /* Match h1 size */
    width: 100px; /* Match h1 size */
    justify-content: center;
}

/* Show tail arrow when stack has at least 2 elements */
.Stack-Show.has-elements .Arro-tail {
    visibility: visible;
}

/* Stack element container */
.stackE {
    display: flex;
    flex-direction: column-reverse; /* Reverse order for LIFO */
    align-items: center;
    gap: 10px;
    border: 2px solid violet;
    border-radius: 8px;
    padding: 10px;
    background: rgba(240, 92, 92, 0.8);
    color: #fff;
    font-size: 1rem;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100px;
    flex-grow: 1; /* Grow to fill available space */
}

/* Individual stack items */
.stackE div {
    width: 80px;
    height: 40px;
    background-color: #007BFF;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: bold;
    border: 1px solid #0056b3;
    transition: all 0.3s ease;
}

.stackE div:hover {
    background-color: #0056b3;
    transform: scale(1.1);
}

/* Empty stack message */
.stackE .empty {
    color: #fff;
    font-size: 1rem;
    font-weight: bold;
    margin-top: 10px;
}

/* Responsive Styles */
@media (max-width: 768px) {
    h1 {
        font-size: 2rem;
    }

    h4 {
        font-size: 1.25rem;
    }

    .Stack {
        padding: 10px;
        width: 95%;
    }

    .Stack-Show {
        width: 100%;
        padding: 10px;
    }

    .stackE {
        width: 80px;
    }

    .stackE div {
        width: 60px;
        height: 30px;
        font-size: 0.9rem;
    }

    button {
        padding: 8px 16px;
        font-size: 0.9rem;
    }

    .Stac {
        font-size: 1rem;
    }
}