/* General Styles */
.Queue {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 5vh;
    font-family: Arial, sans-serif;
}

button {
    margin: 5px;
    padding: 1rem 1.5rem; /* Use relative units for padding */
    border: none;
    border-radius: 6px;
    background-color: #007BFF;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.Queue-Display {
    font-size: 1rem;
    margin-bottom: 10px;
    color: #333;
}

.Queue-Show {
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    background-color: #ffc107;
    border-radius: 12px;
    padding: 1rem; /* Use relative units for padding */
    border: 2px solid #ff9800;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-top: 1rem; /* Use relative units for margin */
    width: 100%; /* Ensure the container takes full width */
    max-width: 100%; /* Prevent the container from exceeding the viewport width */
    overflow-x: auto; /* Enable horizontal scrolling */
}

/* Arrow Styles */
.Arrow-front,
.Arrow-rear {
    display: flex;
    align-items: center;
    font-weight: bold;
    padding: 5px;
    border-radius: 6px;
    color: white;
    gap: 5px;
    font-size: 0.9rem; /* Fixed font size for arrows */
    white-space: nowrap; /* Prevent text wrapping */
    flex-shrink: 0; /* Prevent arrows from shrinking */
}

.Arrow-front {
    background-color: orange;
}

.Arrow-rear {
    background-color: green;
}

.Arrow-front img,
.Arrow-rear img {
    width: 20px; /* Fixed size for arrow images */
    height: 20px;
}

/* Queue Elements */
.queueElements {
    display: flex;
    
    flex-direction: row;
    gap: 5px;
    border: 2px solid violet;
    border-radius: 8px;
    padding: 10px;
    background: rgba(240, 92, 92, 0.8);
    color: #fff;
    font-size: 1rem;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: fit-content;
    min-width: 60%; /* Ensure the queue elements take full width */
    flex-shrink: 0; /* Prevent queue elements from shrinking */
}

.queueElements div {
    background-color: #007BFF;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: bold;
    border: 1px solid #0056b3;
    transition: all 0.3s ease;
    width: 40px;
    height: 40px;
    flex-shrink: 0; /* Prevent shrinking of queue elements */
}

.queueElements div:hover {
    background-color: #0056b3;
    transform: scale(1.1);
}

.queueElements .empty {
    color: #fff;
    font-size: 1rem;
    font-weight: bold;
    margin-top: 10px;
}

/* Responsive Design */
@media (max-width: 768px) {
    .Queue-Show {
        padding: 0.5rem; /* Adjust padding for smaller screens */
    }

    .Arrow-front,
    .Arrow-rear {
        font-size: 0.8rem; /* Smaller font size for arrows on smaller screens */
    }

    .Arrow-front img,
    .Arrow-rear img {
        width: 15px; /* Smaller arrow images for smaller screens */
        height: 15px;
    }

    .queueElements div {
        width: 30px; /* Smaller queue elements for smaller screens */
        height: 30px;
        font-size: 0.9rem;
    }
}

@media (max-width: 480px) {
    .Queue-Show {
        flex-direction: column; /* Stack arrows and queue elements vertically on very small screens */
        align-items: center;
    }

    .Arrow-front,
    .Arrow-rear {
        margin: 5px 0; /* Add margin between arrows and queue elements */
    }

    .queueElements {
        padding: 5px; /* Reduce padding for smaller screens */
    }
}