


.footer {
    background-color: #50D8F7;
    color: black;
    padding: 15px; /* Adjusted padding for better responsiveness */
    text-align: center;
}

@media (max-width: 768px) {
    .footer {
        padding: 10px; /* Further reduced padding for smaller screens */
    }
}


.footer-title {
    font-size: 20px; /* Adjusted font size for better responsiveness */

    font-weight: bold;
}

.footer-nav {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 10px;
}

.footer-link {
    font-size: 16px; /* Adjusted font size for better responsiveness */

    font-weight: medium;
    text-decoration: none;
    color: black;
}

.footer-link:hover {
    text-decoration: underline;
}

.footer-subtitle {
    font-weight: bold;
    margin-top: 15px;
}

.footer-icons {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 10px;
}

.footer-icons a {
    font-size: 24px;
    transition: transform 0.3s;
}

.footer-icons a:hover {
    transform: scale(1.1);
}

.facebook { color: #1877F2; }
.instagram { color: #E4405F; }
.linkedin { color: #0A66C2; }
.twitter { color: #1DA1F2; }
.github { color: black; }
