.box {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 2px solid skyblue;
    border-radius: 8px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    min-width: 120px; /* Minimum width for each node */
    margin-right: 10px; /* Space between nodes */
    flex-shrink: 0; /* Prevent shrinking */
}

.para {
    display: block; /* Change to block for better layout control */
    margin: 5px 0;
    font-size: 1rem;
    color: #333;
    text-align: center;
    border: 2px solid skyblue;
    padding: 10px;
}

.design {
    display: flex;
    overflow-x: auto; /* Enable horizontal scrolling */
    white-space: nowrap; /* Ensure content does not wrap */
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 12px;
    margin-top: 20px;
}

.box > img {
    margin-top: 10px;
    height: 20px;
    width: auto;
}

.Null {
    margin-top: 10px;
    font-size: 1rem;
    color: #333;
    font-weight: bold;
}

/* Responsive Styles */
@media (max-width: 768px) {
    h1 {
        font-size: 2rem;
    }

    h3, h4 {
        font-size: 1.25rem;
    }

    .design {
        padding: 10px;
    }

    .box {
        min-width: 100px;
    }

    .para {
        font-size: 0.9rem;
    }
}
