:root {
  --primary-color: #1976d2;
  --primary-hover: #115293;
  --secondary-color: #dc004e;
  --secondary-hover: #9a0036;
  --background-light: #f9f9f9;
  --border-color: #ddd;
  --text-color: #333;
}

/* 🏠 Container Styling */
.tree-container {
  text-align: center;
  margin-top: 20px;
  font-family: 'Arial', sans-serif;
  padding: 10px;
}

/* 🎨 Button Styling */
.tree-button {
  margin: 5px;
  padding: 10px 20px;
  font-size: clamp(12px, 2vw, 16px);
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.tree-button:active {
  transform: scale(0.95);
}

.tree-button.primary {
  background-color: var(--primary-color);
  color: white;
}

.tree-button.primary:hover {
  background-color: var(--primary-hover);
}

.tree-button.secondary {
  background-color: var(--secondary-color);
  color: white;
}

.tree-button.secondary:hover {
  background-color: var(--secondary-hover);
}

/* 📜 Scrollable Tree SVG Container */
.tree-svg-container {
  width: 100%;
  height: 80vh; /* Adjust height */
  overflow-x: auto; /* Horizontal scrolling */
  overflow-y: auto; /* Vertical scrolling */
  white-space: nowrap;
  display: flex;
  justify-content: flex-start; /* Left margin fix */
  align-items: flex-start;
  padding: 50px 10px 10px; /* Added top padding */
  position: relative;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background-color: var(--background-light);
}

/* 🌳 Tree SVG Styling */
.tree-svg {
  min-width: 1000px; /* Ensures horizontal scrolling */
  max-width: 100%;
  height: auto;
  max-height: 700px;
  overflow: visible;
  transform-origin: center;
  margin-left: 20px; /* Left margin fix */
  margin-top: 10px;
}

/* 🔵 Tree Node Styling */
.tree-node {
  fill: lightblue;
  stroke: black;
  stroke-width: 1.5px;
  r: clamp(10px, calc(15px + 0.5vw), 20px);
  transition: fill 0.3s ease;
}

.tree-node.highlighted {
  fill: red;
}

/* ✍️ Tree Text Styling */
.tree-text {
  font-size: clamp(10px, 1.5vw, 14px);
  fill: var(--text-color);
  text-anchor: middle;
}

/* 🌿 Tree Edge Styling */
.tree-edge {
  stroke: black;
  stroke-width: clamp(1.5px, 0.2vw, 2px);
}

/* 🎛️ Controls and Results Container */
.tree-controls-results {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

/* 🕹️ Controls Styling */
.tree-controls {
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
}

/* 🔍 Search Input Styling */
.tree-search-input {
  width: 200px;
}

/* 📜 Traversal Result Styling */
.traversal-result {
  margin-top: 10px;
  font-size: clamp(14px, 2vw, 18px);
  color: var(--text-color);
}

/* ⬇️ Dropdown Styling */
.tree-type-dropdown {
  margin-bottom: 20px;
}

/* 📱 Responsive Adjustments */
@media (max-width: 800px) {
  .tree-svg-container {
    overflow-x: auto; /* Ensure horizontal scroll */
    overflow-y: auto; /* Ensure vertical scroll */
    padding-bottom: 10px;
    align-items: center;
  }

  .tree-svg {
    min-width: 700px;
    max-width: 100%;
    transform: scale(0.9);
    margin-left: 0; /* Center align on small screens */
  }

  .tree-node {
    r: 12px;
  }

  .tree-text {
    font-size: 12px;
  }

  .tree-controls {
    flex-direction: column;
    align-items: center;
  }

  .tree-search-input {
    width: 100%;
  }
}

/* 🌙 Dark Mode Support */
@media (prefers-color-scheme: dark) {
  :root {
    --background-light: #1e1e1e;
    --border-color: #444;
    --text-color: #f9f9f9;
  }

  .tree-svg {
    background-color: var(--background-light);
  }

  .tree-text {
    fill: var(--text-color);
  }

  .tree-node {
    fill: #2c3e50;
  }

  .tree-node.highlighted {
    fill: #e74c3c;
  }

  .tree-edge {
    stroke: #f9f9f9;
  }
}
